import React, { Component } from 'react'
import { Link } from 'react-router-dom'

class Header extends Component {
    
    constructor(props) {
        super(props)
        this.state = {classCard: 'header-site'}
    }

    handleAnimation = () => {       
        if (document.documentElement.scrollTop > 50) {           
         this.setState({ classCard: 'header-site header-fixed' });  
       }
       else {
           this.setState({classCard: 'header-site'})
       }
    }

    componentDidMount() {
        window.onscroll = () => this.handleAnimation();  
    }
    
       render() {
        return (
        <header className={this.state.classCard} role="banner">
            <div className="logo-header">
                <img alt="Logo del sitio" src="https://yt3.ggpht.com/a/AATXAJwJQhM07X_Na736UooEfxnZ6Kcnrl3ZRrSvOw=s900-c-k-c0xffffffff-no-rj-mo" />
            </div>
            <ul className="list-nav">
                <li><Link to="/" >Inicio</Link></li>
                <li><Link to="emailto:hola@qonexia.com">Contactanos</Link></li>
                <li><Link to="" >Catalogos</Link></li>
                <li><Link to="" >Promociones</Link></li>
            </ul>
        </header>
        )
    }    
}

export default Header;